
import React from 'react';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
import './professionaltemplate1.css';
import StrToListModule from './strtolistmodule';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLocationDot } from '@fortawesome/free-solid-svg-icons';
import { faMobileScreenButton } from '@fortawesome/free-solid-svg-icons';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { faLinkedinIn } from '@fortawesome/free-brands-svg-icons';
import { faCalendarDays } from '@fortawesome/free-solid-svg-icons';
import { faPercent } from '@fortawesome/free-solid-svg-icons';
import { faBuilding } from '@fortawesome/free-solid-svg-icons';
import { faMapPin } from '@fortawesome/free-solid-svg-icons';
import { faLaptopCode } from '@fortawesome/free-solid-svg-icons';
import '@fontsource/roboto';
import '@fontsource/poppins';

function Professionaltemplate3({formData}){
	// string to list 
	const {interestList,	hardSkillsList,	softSkillsList,	expDescriptionList} = StrToListModule(formData);

// Function to save as PDF----------------------------------------------------------------------
const handleSaveAsPDF = () => {
  const input = document.getElementById('professionaltemplate3');

  // Adjust scale dynamically
  const scale = window.devicePixelRatio > 2 ? 3 : 2; 

  html2canvas(input, { scale }).then((canvas) => {
    const imgData = canvas.toDataURL('image/png');
    const pdf = new jsPDF('p', 'mm', 'a4');
    
    const pdfWidth = pdf.internal.pageSize.getWidth();
    const pdfHeight = pdf.internal.pageSize.getHeight();
    const pageHeight = (canvas.width / pdfWidth) * pdfHeight;
    const margin = 10; // Adjust this value to control the gap between pages

    let position = 0;
    let page = 0;

    while (position < canvas.height) {
      // Create a new canvas for each page
      const pageCanvas = document.createElement('canvas');
      pageCanvas.width = canvas.width;
      pageCanvas.height = pageHeight;

      const pageContext = pageCanvas.getContext('2d');
      pageContext.fillStyle = '#FFFFFF'; // Set background to white to avoid transparency issues
      pageContext.fillRect(0, 0, pageCanvas.width, pageCanvas.height);

      // Draw the part of the original canvas on the new page canvas
      pageContext.drawImage(
        canvas,
        0,
        position,
        canvas.width,
        pageHeight,
        0,
        0,
        canvas.width,
        pageHeight
      );

      const pageImgData = pageCanvas.toDataURL('image/png');

      // Add the image to the PDF with a margin
      if (page > 0) pdf.addPage();
      pdf.addImage(pageImgData, 'PNG', 0, margin, pdfWidth, pdfHeight - margin * scale);

      position += pageHeight;
      page++;
    }

    // Save the PDF file
    pdf.save(`${formData.personalInfo.fullName || 'resume'}.pdf`);
  });
};



	
	return(
		<>
		{/*Model---------------------------------------------------------------------------*/}
				

				<div className="modal fade" id="professionalTemplateModel3" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLongTitle" aria-hidden="true">
				  <div className="modal-dialog modal-xl" role="document">
				    <div className="modal-content">
				      <div className="modal-header  justify-content-between align-items-center model-hd-head">
				        <h5 className="modal-title col-10" id="exampleModalLongTitle">PROFESSIONAL - TEMPLATE 3</h5>
				        <button type="button" className="close  btn col-2" data-bs-dismiss="modal" aria-label="Close">
				          <span aria-hidden="true">&times;</span>
				        </button>
				      </div>
				      <div className="modal-body row justify-content-center align-items-start profTemp1-cover" id="professionaltemplate3">
				      	
				      	<div className="col-12 profName-box">
				      		<h1>{formData.personalInfo.fullName.toUpperCase()}</h1>
				      		<h2>{formData.personalInfo.designation.toUpperCase()}</h2>
				      	</div>
				      	
				      	<div className="col-4">
				      		<div className="row justify-content-between align-items-start">
				      			<div className="col-11 profContact-box prof-box-common">

				      				<h3>CONTACT</h3>	
				      				<ul>
						        		<li><FontAwesomeIcon className="contact-list-icons" icon={faLocationDot} />{formData.contact.address},{formData.contact.city},{formData.contact.country}</li>
						        		<li><FontAwesomeIcon className="contact-list-icons" icon={faMapPin} />{formData.contact.pinCode}</li>
						        		<li><FontAwesomeIcon className="contact-list-icons" icon={faMobileScreenButton} />{formData.contact.phoneNo}</li>
						        		<li><FontAwesomeIcon className="contact-list-icons" icon={faEnvelope} />{formData.contact.email}</li>
						        		<li><FontAwesomeIcon className="contact-list-icons" icon={faLinkedinIn} />{formData.contact.linkedin}</li>
						        	</ul>
				      			</div>

				      			<div className="col-11 profSkill-box prof-box-common"> 
				      				<h3>SKILLS</h3>
				      				<h4>Hard Skills</h4>
						        	<ul className="skills-list">
						        		{hardSkillsList.map((item, index)=>(
						        			<li>{item.trim()}</li>
						        		))}
						        	</ul>
						        	<h4>Soft Skills</h4>
						        	<ul className="skills-list">
						        		{softSkillsList.map((item, index)=>(
						        			<li>{item.trim()}</li>
						        		))}
						        	</ul>	
				      			</div>

				      			<div className="col-11 profEducation-box prof-box-common"> 
				      				<h3>EDUCATION</h3>	
				      				{formData.higherEducation.map((edu, index) => (
											  <p key={index}><span>{edu.degree}</span> <br/>
								        			{edu.institution} <br/>
								        			<FontAwesomeIcon className="education-list-icons" icon={faCalendarDays} />{edu.passingYear} &nbsp;&nbsp;&nbsp;&nbsp; <FontAwesomeIcon className="education-list-icons" icon={faPercent} />{edu.GPA} %
								        	</p>
											))}	
				      			</div>

				      			<div className="col-11 profAward-box prof-box-common"> 
				      				<h3>AWARD</h3>	
				      				{formData.award.map((awd, index) => (
											  <p key={index}><span>{awd.awardName} </span><br/>
							        			{awd.awardCompany}  &nbsp;&nbsp;&nbsp;&nbsp; - &nbsp;&nbsp;&nbsp;&nbsp;{awd.awardDate}<br/>
							        			{awd.awardDescription}
								        </p>
											))}	
				      			</div>

				      			<div className="col-11 profAward-box prof-box-common"> 
				      				<h3>ACHIEVEMENT</h3>	
				      				{formData.achievement.map((ach, index) => (
											  <p key={index}><span>{ach.achievementName} </span><br/>
							        			{ach.achievementDescription}
								        </p>
											))}	
				      			</div>

				      			<div className="col-11 profLanguage-box prof-box-common"> 
				      				<h3>LANGUAGE</h3>	
				      				<p>{formData.personalInfo.language}</p>
				      			</div>

				      		</div>
				      	</div>
				      	
				      	<div className="col-8">
				      		<div className="row justify-content-center align-items-start">

				      			<div className="col-12 profSummary-box prof-box-common"> 
				      				<h3>PROFILE</h3>	
				      				<p>{formData.profileSummary.profileSummary}</p>
				      			</div>

				      			<div className="col-12 profProject-box prof-box-common"> 
				      				<h3>PROJECT</h3>	
				      				{formData.project.map((prj, index) => (
									  <p key={index}><span>{prj.projectTitle}</span> <br/>
						        			{prj.client} <br/>
						        			<FontAwesomeIcon className="project-list-icons" icon={faLaptopCode} />{prj.skillUsed}
								        </p>
									))}
				      			</div>

				      			<div className="col-12 profExperience-box prof-box-common"> 
				      				<h3>EXPERIENCE</h3>	
				      				{formData.experience.map((expp, index) => (
											  <div key={index}>
											    <p>
											      <span>{expp.jobTitle}</span> <br />
											      <FontAwesomeIcon className="education-list-icons" icon={faBuilding} />
											      {expp.company} &nbsp;&nbsp;&nbsp;&nbsp; - &nbsp;&nbsp;&nbsp;{expp.duration}
											    </p>
											    <ul>
											      {expp.description && expp.description.split('/').map((item, idx) => (
											        <li key={idx}>{item.trim()}</li>
											      ))}
											    </ul>
											  </div>
											))}
				      			</div>

				      		</div>
				      	</div>
				        

				      </div>
				      <div className="modal-footer model-hd-ft">
				        <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
				        <button type="button" className="btn btn-primary" onClick={handleSaveAsPDF}>Download Resume</button>
				      </div>
				    </div>
				  </div>
				</div>   
			{/*Model---------------------------------------------------------------------------*/}
			</>
		);
}

export default Professionaltemplate3;